/*          --TITLE---          */
.Title{
    padding: 70px 0;
    font-family: 'Circular Std';
    height: 400px;
}
.t_btn_contact{
    background-color: #3E82E5;
    color: white;
    width: 230px;
    height: 70px;
    border-radius: 7px;
    margin-top: 30px;
    font-weight: bold;
    font-size: large;
    cursor: pointer;
    border: #3E82E5 1px solid;
    box-shadow: 0;
    transition: box-shadow 0.5s;  
}
.t_btn_contact:hover{
    box-shadow: 2px 2px 70px #3E82E5;
}

/*      ---Carousel---      */

.Carousel{
    display: flex;
    margin: 0 auto;
    height: 320px;
    margin-bottom: 210px;

}
.slide{
    width: 350px;
    height: 350px;
    background-color: #28292F;
    margin-right: 50px;
    padding: 0px;
    border-radius: 5px;
    box-shadow: 0;
    transition: box-shadow 0.5s;  
    transition: all 0.5s;
    color: white;
    position: relative;
}
.slide:hover{
    width: 370px;
    height: 370px;
    box-shadow: 2px 2px 30px #3E82E5;
   box-shadow:         inset 0 0 100px #000000;
}
.slide_web{
    background-image: url(/tedora.info/static/media/carousel_web.1a2d0f89.jpeg);
    background-size: cover;
    background-position:  left; 
}
.slide_mob{
    background-image: url(/tedora.info/static/media/carousel_mob.d4467429.png);
    background-size: cover;
    background-position: left; 
}
.slide_bottom{
    font-family: 'Roboto', sans-serif;
    color: white;
    text-align: center;
    font-size: 8px;
    position: absolute;
    margin-left: 40px;
    margin-bottom: 10px;
    bottom: 0;
}
.slide_bottom h1{
    font-family: 'Circular Std';
    color: #000000;
    overflow: hidden;
    font-weight: bold;
    padding: 7px;
    border-radius: 5px;
    text-align: left;
}

.slide_bottom h1:hover{
    -webkit-animation: openclose 15s ease-in-out infinite;
            animation: openclose 15s ease-in-out infinite;
    background-color: #56657F;
    overflow: hidden;
    color: white;
    text-align: left;
}
@-webkit-keyframes openclose {
    0% {
      top: 0.2rem;
      width: 130px;
    }
    5% {
      width: 130px;
    }
    15% {
      width: 230px;
    }
    30% {
      top: 0.2rem;
      width: 230px;
    }
    33% {
      top: 0.2rem;
      width: 130px;
    }
    35% {
      top: 0.2rem;
      width: 130px;
    }
    38% {
      top: -4.5rem;
      
    }
    48% {
      top: -4.5rem;
      width: 190px;
    }
    62% {
      top: -4.5rem;
      width: 190px;
    }
    66% {
      top: -4.5rem;
      width: 130px;
      text-indent: 0;
    }
    71% {
      top: -9rem;
      width: 130px;
      text-indent: 5px;
    }
    86% {
      top: -9rem;
      width: 285px;
    }
    95% {
      top: -9rem;
      width: 285px;
    }
    98% {
      top: -9rem;
      width: 130px;
      text-indent: 5px;
    }
    100% {
      top: 0;
      width: 130px;
      text-indent: 0;
    }
  }
@keyframes openclose {
    0% {
      top: 0.2rem;
      width: 130px;
    }
    5% {
